@import 'fonts'; // Шрифты
@import 'reset'; // Сброс стилей
@import 'setting'; // Общие настройки
@import 'vars'; // Переменные
@import 'mixins'; // Миксины


// Компоненты 
@import 'components/footer';
@import 'components/header';


body, button {
    font-family: 'Montserrat', sans-serif;
}



// Стили



.main-screen {
    &__inner {
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding: 40px;
        padding-right: 0;
        background: linear-gradient(180deg, #E6F0FF 0%, #F0F6FF 69.8%, rgba(243, 248, 251, 0) 100%);
        border-radius: 16px;
        position: relative;

        @media(max-width: 991px){
            padding-top: 80px !important;
            min-height: 365px;
        }


        @media(max-width: 768px) {
            flex-direction: column-reverse;
            padding: 15px;
            background: linear-gradient(180deg, #E6F0FF 0%, #F0F6FF 69.8%, rgba(243, 248, 251, 0) 100%);
            padding-top: 0 !important;
        }

        
    .ref-btns {
        display: flex;
        align-items: center;
        margin-top: 30px;

        @media(max-width: 768px) {
            flex-direction: column;
            margin-top: 20px;
        }

        .blue-btn {
            margin-top: 0;
            height: 48px;
            display: flex;
            align-items: center;
            margin-right: 20px;
            @media(max-width: 768px) {
                width: 100%;
                text-align: center;
                justify-content: center;
                margin-right: 0;
                margin-bottom: 15px;
            }
        }

        .ref-link {
            font-weight: 700;
            font-size: 13px;
            line-height: 140%;
            color: #3A75FF;
            display: flex;
            align-items: center;
            position: relative;
            transition: 0.2s linear;

            &:hover {
                opacity: 0.6;
            }

            span {
                padding-bottom: 2px;
                border-bottom: 1px dashed #3A75FF;
            }


            svg {
                margin-right: 5px;
            }
        }
    }

       
    }


    .bonus-count{
        padding: 6px 16px;
        background: linear-gradient(82.2deg, #9E91F1 0.29%, #4C7EF2 91.06%);
        border-radius: 8px;
        font-weight: 700;
        font-size: 13px;
        line-height: 120%;
        color: #FFFFFF;
        margin-bottom: 10px;
        width: max-content;
        margin-top: 65px;
        &-closed{
            background: linear-gradient(82.2deg, #FF2F61 0.29%, #D32A3F 91.06%);
        }
    }

    

    .country-wrapper{
        display: flex;
        align-items: center;
        font-weight: 400;
        font-size: 14px;
        line-height: 140%;
        color: #303133;
        position: absolute;
        top: 30px;
        left: 60px;
        @media(max-width: 991px){
            top: 16px;
            left: 60px;
          
            margin-bottom: 20px;
        }

        @media(max-width: 768px){
            left: 10px;
            flex-direction: column;
            align-items: flex-start;
        }
        .country{
            display: flex;
            align-items: center;
            b{
                font-weight: 600;
                padding-left: 5px;
            }
            &-choice{
                font-weight: 500;
                font-size: 14px;
                line-height: 140%;
                color: #3A75FF;
                display: block;
                &:hover{
                    opacity: 0.8;
                }
                margin-left: 20px;
                width: max-content;
                background: none;
                border: none;
                cursor: pointer;

                @media(max-width: 768px){
                    margin-left: 0;
                    margin-top: 2px;
                    padding: 0;
                }
            }
        }
        .tooltip{
            .icon{
                width: 16px;
                height: 16px;
                background-image: url("data:image/svg+xml,%3Csvg width='16' height='16' viewBox='0 0 16 16' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Ccircle cx='8' cy='8' r='8' fill='white'/%3E%3Cpath d='M7.43575 9.21111C7.43575 8.82593 7.52142 8.50741 7.69274 8.25556C7.87151 8.0037 8.12849 7.72593 8.46369 7.42222C8.7095 7.2 8.88827 7.01481 9 6.86667C9.11918 6.71111 9.17877 6.53704 9.17877 6.34444C9.17877 6.07037 9.06704 5.85556 8.84358 5.7C8.62756 5.53704 8.33706 5.45556 7.97207 5.45556C7.62197 5.45556 7.30912 5.52963 7.03352 5.67778C6.76536 5.81852 6.53817 6.01852 6.35196 6.27778L5 5.48889C5.31285 5.01481 5.73371 4.64815 6.26257 4.38889C6.79888 4.12963 7.43203 4 8.16201 4C9.01862 4 9.70391 4.18148 10.2179 4.54444C10.7393 4.90741 11 5.41111 11 6.05556C11 6.35926 10.9479 6.62593 10.8436 6.85556C10.7467 7.08519 10.6238 7.28148 10.4749 7.44444C10.3333 7.6 10.1471 7.78148 9.9162 7.98889C9.6406 8.23333 9.43948 8.44074 9.31285 8.61111C9.18622 8.77407 9.12291 8.97407 9.12291 9.21111H7.43575ZM8.28492 12C7.97952 12 7.72253 11.9037 7.51397 11.7111C7.31285 11.5111 7.21229 11.2704 7.21229 10.9889C7.21229 10.7074 7.31285 10.4741 7.51397 10.2889C7.71508 10.0963 7.97207 10 8.28492 10C8.59777 10 8.85475 10.0963 9.05587 10.2889C9.25698 10.4741 9.35754 10.7074 9.35754 10.9889C9.35754 11.2704 9.25326 11.5111 9.04469 11.7111C8.84358 11.9037 8.59032 12 8.28492 12Z' fill='%233A75FF'/%3E%3C/svg%3E%0A");
                background-size: cover;
                background-position: center;
                margin-left: 6px;
                display: block;
                cursor: pointer;
            }
            position: relative;
            

            .content{
                opacity: 0;
                visibility: hidden;
                background: #393939;
                border-radius: 8px;
                position: absolute;
                top: 25px;
                left: 65%;
                transform: translateX(-50%);
                padding: 10px 16px;
                color: #fff;
                width: 250px;
                transition: 0.2s linear;
                text-align: center;
                font-weight: 400;
                font-size: 12px;
                line-height: 140%;
                &::after {
                    content: " ";
                    position: absolute;
                    bottom: 100%;  /* At the top of the tooltip */
                    left: 50%;
                    margin-left: -5px;
                    border-width: 5px;
                    border-style: solid;
                    border-color: transparent transparent #393939 transparent;
                }
            }

            &:hover{
                .content{
                    visibility: visible;
                    opacity: 1;
                }
            }
        }
    }

    &__content {
        margin-top: -50px;
        @media(max-width: 768px) {
            margin-top: 10px;
            margin-right: 0;
        }

        h1 {
            font-weight: 700;
            font-size: 26px;
            line-height: 140%;
            color: #303133;

            @media(max-width: 768px) {
                font-size: 24px;
            }
        }

        p {
            font-weight: 500;
            font-size: 15px;
            line-height: 180%;
            color: #303133;
            margin-top: 20px;
            max-width: 450px
        }

        .place{
            font-weight: 500;
            font-size: 20px;
            line-height: 160%;
            display: inline-block;
            padding-top: 8px;
        }
    }

    &__img {
        max-width: 420px;
        width: 100%;
        margin-top: -40px;
        @media(max-width: 768px) {
            margin-bottom: 20px;
            margin-top: 0;
        }

        img {
            width: 100%;
            object-fit: cover;
        }
    }

    
}


.timer {
    display: flex;
    padding: 17px;
    background: rgba(255, 255, 255, 0.6);
    border-radius: 16px;
    width: max-content;
    margin-bottom: 25px;

    div {
        display: flex;
        flex-direction: column;
        align-items: center;
        position: relative;
        min-width: 40px !important;
        max-width: 40px !important;
    }

    svg {
        margin: 0px 6px;
        margin-top: 12px;
    }

    .number {
        font-style: normal;
        font-weight: 600;
        font-size: 26px;
        line-height: 32px;
        color: #303133;
    }

    .title {
        font-weight: 500;
        font-size: 12px;
        line-height: 15px;
        color: #303133;
        text-transform: uppercase;
    }
}

.text{
    &__inner{
        padding-top: 40px;
        padding-bottom: 90px;
        font-weight: 400;
        font-size: 20px;
        line-height: 140%;
        color: #303133;

        @media(max-width: 768px){
            font-weight:500;
            font-size: 15px;
            line-height: 140%;
            padding-bottom: 45px;
            padding-top: 10px;
            padding: 0px 10px;
        }
      
    }
}
