            iframe{
                height: 400px;

            }
        }

        @media(max-width: 580px) {
            iframe{
                height: 320px;
            }
        }

        @media(max-width: 480px) {
            iframe{
                height: 250px;
            }
        }

        @media(max-width: 400px) {
            iframe{
                height: 200px;
            }
        }

        img {
            width: 100%;
            object-fit: cover;
        }
    }

    &__content {
        // overflow-y: auto;
        max-height: 367px;
        scrollbar-width: thin;
      
        
        b{
            font-weight: 600;
        }
       

        @media(max-width: 768px) {
            max-height: 100%;
           
        }

        .link{
            font-weight: 600;
            font-size: 18px;
            line-height: 160%;
            color: #4C68EF;
            display: inline-block;
            margin-top: 42px;
            &:hover{
                color: #2340CD;
            }

            @media(max-width: 768px){
                font-weight: 600;
                font-size: 16px;
                line-height: 160%;
                margin-top: 20px;
            }
        }

        .name {
            font-weight: 700;
            font-size: 16px;
            line-height: 180%;
            color: #303133;

