}



.blue-btn {
    font-weight: 700;
    font-size: 15px;
    line-height: 180%;
    color: #FFFFFF;
    padding: 7px 35px;
    width: max-content;
    background: #3A75FF;
    border-radius: 16px;
    margin-top: 30px;
    display: block;
    transition: 0.2s linear;

    &:hover {
        box-shadow: 0px 12px 18px rgba(58, 117, 255, 0.4);
        background: #3A75FF;
    }
}




.copy-link {
    position: relative;

    &::before {
        position: absolute;
        content: 'Скопировано';
        background: #555;
        padding: 10px 15px;
        width: max-content;
        border-radius: 5px;
        color: #fff;
        top: -45px;
        left: 50%;
        transform: translateX(-50%) translateY(20px);
        font-size: 12px;
        opacity: 0;
        transition: 0.1s linear;
    }
}

.copy-link.visible {
    &::before {
        transform: translateX(-50%) translateY(0px);
        opacity: 1;
    }

}

.examples {
    margin-top: 70px;
    &-title{
        font-weight: 400;
        font-size: 26px;
        line-height: 140%;
        color: #303133;
        padding-left: 20px;
        margin-bottom: 40px;
        b{
            font-weight: 600;
        }

        @media(max-width: 768px) {
            font-weight:400;
            font-size: 22px;
            line-height: 140%;
            margin-bottom: 20px;
            padding-left: 10px;
        }
    }
    @media(max-width: 768px) {
        margin-top: 40px;
        margin-bottom: 50px;
    }

    &-slider{
        overflow: hidden;
        padding: 20px;
        @media(max-width: 768px){
            padding: 10px;
        }
        &-nav{
            display: flex;
            width: 100%;
            justify-content: center;
            align-items: center;
            margin-top: 20px;
            @media(max-width: 768px) {
                margin-top: 15px;
            }
            .prev-slide, .next-slide{
                width: 24px;
                height: 24px;
                display: flex;
                justify-content: center;
                align-items: center;
                cursor: pointer;
                &:hover{
                    svg{
                        path{
                            stroke: #3A75FF;
                        }
                    }
                }
            }

            .slider-pagination{
                width: max-content !important;
                display: flex;
                align-items: center;
                margin: 0px 10px;
                .swiper-pagination-bullet{
                    width: 11px;
                    height: 11px;
                    margin-top: 0;
                    cursor: pointer;
                    background: rgba(160, 182, 209, 0.31);
                    opacity: 1 !important;

                    &.swiper-pagination-bullet-active{
                        background: #3A75FF;
                    }
                    &:hover{
                        background: #1353E9;
                    }
                }

                
            }
        }
    }

    .container{
        max-width: 1040px;
        @media(max-width: 768px){
            padding: 0;
        }
    }



    &__slider-wrapper{
        display: flex;

      
    }

    &__slide{
        // width: 50%;
        flex-shrink: 0;
        img{
            width: 100%;
            height: auto;
            object-fit: cover;
        }
        @media(max-width: 768px) {
            width: 100%;
        }

        @media(max-width: 768px){
            min-height: 280px;
        }

        .title {
            font-weight: 700;
            font-size: 20px;
            line-height: 140%;
            padding-top: 20px;
            padding-bottom: 10px;
            color: #303133;

            @media(max-width: 768px) {
                font-weight: 700;
                font-size: 16px;
                line-height: 160%;
                padding-top: 20px;
                padding-bottom: 8px;
            }
        }

        .text {
            font-weight: 400;
            font-size: 14px;
            line-height: 180%;

            @media(max-width: 768px) {
                margin-top: 0px;
                font-weight: 400;
                font-size: 14px;
                line-height: 180%;
            }
        }
    }
}


