.faq {
    margin-top: 80px;
    @media(max-width: 768px){
        margin-top: 40px;
    }
    &-title {
        font-weight: 700;
        font-size: 28px;
        line-height: 140%;
        color: #303133;
        margin-bottom: 50px;
        @media(max-width: 768px){
            margin-bottom: 25px;
        }
    }

    .ac {
        background: #E8F2FF;
        border-radius: 16px;
        border: none;
        transition: 0.2s linear;
        font-family: 'Montserrat', sans-serif;

        .ac-trigger {
            border: none;
            border-radius: none;
            padding: 21px;
            padding-left: 60px;
            border: none;
            font-weight: 500;
            font-size: 20px;
            line-height: 140%;
            color: #303133;
            font-family: 'Montserrat', sans-serif;

            @media(max-width: 768px) {
                font-size: 15px !important;
                padding: 15px;
                padding-left: 45px;
            }

            &:after {
                content: '';
                right: auto;
                left: 15px;
                background-image: url("data:image/svg+xml,%3Csvg width='34' height='34' viewBox='0 0 34 34' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M17 8.5V17M17 17V25.5M17 17H25.5M17 17H8.5' stroke='%233A75FF' stroke-width='3' stroke-linecap='round' stroke-linejoin='round'/%3E%3C/svg%3E%0A");
                width: 34px;
                height: 34px;
                background-size: cover;

                @media(max-width: 768px) {
                    left: 5px;
                }
            }
        }

        .ac-text {
            font-weight: 400;
            font-size: 15px;
            line-height: 180%;
            color: #303133;
            padding-left: 60px;
            padding-right: 60px;
            padding-top: 0;
            padding-bottom: 30px;
            font-family: 'Montserrat', sans-serif;
            ul{
                font-weight: 400;
                font-size: 15px;
                line-height: 180%;
                color: #303133;
                padding-right: 60px;
                padding-top: 0;
                padding-bottom: 30px;
                @media(max-width: 768px) {
                    padding-right: 0;
                }
                li{
                    position: relative;
                    padding-left: 20px;
                    @media(max-width: 768px) {
                        font-size: 13px !important;
                    }
                    &::before{
                        content: '';
                        position: absolute;
                        display: block;
                        width: 3px;
                        height: 3px;
                        border-radius: 50%;
                        top: 12px;
                        left: 0;
                        background: #303133;
                    }
                }


            }
            @media(max-width: 768px) {
                font-size: 13px !important;
                padding-left: 45px;
            }
        }
    }

    .ac.is-active>.ac-header .ac-trigger::after {
        content: '';
        background-image: url("data:image/svg+xml,%3Csvg width='34' height='34' viewBox='0 0 34 34' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M25.5 17H17H8.5' stroke='%234EC5DE' stroke-width='3' stroke-linecap='round' stroke-linejoin='round'/%3E%3C/svg%3E%0A") !important;
    }

    .ac.is-active {
        background: #F4F8FE;
    }


}


