.gallery{
    &-title{
        font-weight: 400;
        font-size: 26px;
        line-height: 140%;
        color: #303133;
        margin-bottom: 50px;
        b{
            font-weight: 600;
        }

        @media(max-width: 768px){
            font-size: 20px;
            line-height: 140%;
            margin-bottom: 25px;
            margin-top: 20px;
        }
    }

    &__inner{
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        grid-gap: 14px;
        @media(max-width: 768px){
            grid-template-columns: repeat(2, 1fr);
            grid-gap: 8px;
        }

        .gallery-item:nth-child(n+10){
            display: none;
        }

        &.all{
            .gallery-item:nth-child(n){
                display: block;
            }
        }
        &__more{
            display: none;
            padding-top: 14px;
            @media(max-width: 768px){
                padding-top: 8px;
            }
            &.active{
                display: grid !important;
            }
        }

        
    }

    &-item{
        border-radius: 16px;
        overflow: hidden;
        img{
            display: block;
            width: 100%;
            height: 100%;
            object-fit: cover;
            border-radius: 16px;
            transition: 0.2s linear;
        }
        position: relative;
        
        &::before{
            content: '';
            width: 100%;
            height: 100%;
            border-radius: 16px;
            position: absolute;
            top: 0;
            left: 0;
            background: linear-gradient(0deg, rgba(55, 55, 55, 0.6), rgba(55, 55, 55, 0.6));
            transition: 0.2s linear;
            opacity: 0;
            z-index: 3;
        }

        &::after{
            content: '';
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            width: 30px;
            height: 30px;
            background-size: cover;
            background-position: center;
            background-repeat: no-repeat;
            background-image: url("data:image/svg+xml,%3Csvg width='34' height='34' viewBox='0 0 34 34' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M32 32L22 22M25.3333 13.6667C25.3333 15.1988 25.0316 16.7158 24.4453 18.1313C23.859 19.5468 22.9996 20.8329 21.9162 21.9162C20.8329 22.9996 19.5468 23.859 18.1313 24.4453C16.7158 25.0316 15.1988 25.3333 13.6667 25.3333C12.1346 25.3333 10.6175 25.0316 9.20203 24.4453C7.78656 23.859 6.50044 22.9996 5.41709 21.9162C4.33374 20.8329 3.47438 19.5468 2.88807 18.1313C2.30177 16.7158 2 15.1988 2 13.6667C2 10.5725 3.22916 7.60501 5.41709 5.41709C7.60501 3.22916 10.5725 2 13.6667 2C16.7609 2 19.7283 3.22916 21.9162 5.41709C24.1042 7.60501 25.3333 10.5725 25.3333 13.6667Z' stroke='white' stroke-width='3' stroke-linecap='round' stroke-linejoin='round'/%3E%3C/svg%3E%0A");
            transition: 0.2s linear;
            opacity: 0;
            z-index: 4;
        }


        &:hover{
            img{
                transform: scale(1.05);
            }
            &::before, &::after{
                opacity: 1;
            }
        }
    }

    .show-more{
        font-weight: 600;
        font-size: 14px;
        line-height: 180%;
        color: #3A75FF;
        &:hover{
            color: darken(#3A75FF, 10%);
        }
        display: block;
        width: max-content;
        margin: 20px auto;
    }
}


.footer-text{
    padding-top: 30px;
    padding-bottom: 50px;

    &__inner{
        text-align: center;
        max-width: 900px;
        font-weight: 400;
        font-size: 26px;
        line-height: 140%;
        margin: 0 auto;
        @media(max-width: 768px){
            font-size: 20px;
        line-height: 140%;
        }
        b{
            font-weight: 600px;
        }
    }
}


